import Modal from 'react-bootstrap/Modal';
/*import Button from "react-bootstrap/Button";
import Nav from "react-bootstrap/Nav";*/

import React, {useState} from 'react'

function BsModal(props) {

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <>
            <span className={"ms-2 bind-a text-primary"} role={"button"} onClick={handleShow}>
                立即绑定
            </span>

            <Modal show={show} onHide={handleClose} id="bind-modal" size={"sm"} backdrop="static" centered>
                <Modal.Header className={"border-0 pb-0"} closeButton>
                    <Modal.Title><i className={props.icon}></i> {props.title}</Modal.Title>
                </Modal.Header>
                <Modal.Body className={"pt-0"}>
                    <section className={" p-3"}>{props.body}</section>

                </Modal.Body>
{/*                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                    <Button variant="primary" onClick={handleClose}>
                        Save Changes
                    </Button>
                </Modal.Footer>*/}
            </Modal>
        </>
    );
}

export default BsModal