import Form from "react-bootstrap/Form";
import FloatingLabel from 'react-bootstrap/FloatingLabel'
import ReactCodeInput from "react-code-input";
import React, {useEffect, useState} from "react";
import useCountDown from "../useCountDown";
import Button from "react-bootstrap/Button";


function BindEmail() {


    const [content, setContent] = useState('获取验证码')
    const [btnDisabled, setBtnDisabled] = useState(false)
    const [MobileCode, SetMobileCode] = useState(false)
    const {count, run} = useCountDown()

    useEffect(() => {
        if (btnDisabled) {
            setContent(`重新发送 ${count}s`)
        }
        if (count === 0) {
            btnStatusReset()
        }
    }, [btnDisabled, count])

    // 按钮点击事件
    function handleBtnClick() {
        setBtnDisabled(true)
        setContent(`重新发送 ${count}s`)
        SetMobileCode(true)
        run()
    }
    function handleBtnClickBack() {
        SetMobileCode(false)
    }

    const btnStatusReset = () => {
        setContent('获取验证码')
        setBtnDisabled(false)
    }


    return (
        <>
            <Form id="bind-email-form">

                <section className={MobileCode ? 'd-none' : 'd-block'}>
                    <FloatingLabel className="mt-4 mb-3 input-group" controlId="bind-email" label="电子邮箱">
                        <Form.Control type="email" className="email" placeholder="name@example.com"
                                      required/>
                        <Form.Control.Feedback type="invalid">
                            请输入正确的邮箱号
                        </Form.Control.Feedback>
                        <button className={"input-group-text"} onClick={handleBtnClick}
                                disabled={btnDisabled}>{content}</button>
                    </FloatingLabel>
                </section>


                <section className={!MobileCode ? 'd-none' : 'd-block'}>
                    <div className="d-flex align-items-center  my-4">
    <span className="fa-stack fa-2x me-2">
<i className="fa-duotone fa-mobile fa-stack-2x"></i>
    <i className="fa-solid fa-message text-primary position-absolute top-0 end-0 m-2"></i>
  </span>
                        <div className="text-muted">
                            验证码已发送到您填写的手机号码上<br/>有效期5分钟，请注意查收
                        </div>
                    </div>
                    <article className={"d-flex justify-content-center flex-wrap"}><ReactCodeInput fields={4}/>

                        <div className={"w-100 text-center"}><Button className="mt-4" onClick={() => SetMobileCode(!MobileCode)}><i
                            className="fa-duotone fa-rotate-left"></i> 上一步</Button></div>
                    </article>



                </section>

            </Form>
        </>
    )
}


function BindMobile() {


    const [content, setContent] = useState('获取验证码')
    const [btnDisabled, setBtnDisabled] = useState(false)
    const [MobileCode, SetMobileCode] = useState(false)
    const {count, run} = useCountDown()

    useEffect(() => {
        if (btnDisabled) {
            setContent(`重新发送 ${count}s`)
        }
        if (count === 0) {
            btnStatusReset()
        }
    }, [btnDisabled, count])

    // 按钮点击事件
    function handleBtnClick() {
        setBtnDisabled(true)
        setContent(`重新发送 ${count}s`)
        SetMobileCode(true)
        run()
    }
    function handleBtnClickBack() {
        SetMobileCode(false)
    }

    const btnStatusReset = () => {
        setContent('获取验证码')
        setBtnDisabled(false)
    }

    return (
        <>

            <Form className={"bind-mobile-form"}>
                <section className={MobileCode ? 'd-none' : 'd-block'}>
                    <FloatingLabel className="mt-4 mb-3 input-group" controlId="bind-email" label="手机号">
                        <Form.Control type="text" placeholder="13*******"
                                      pattern="^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$"
                                      required=""/>
                        <button className={"input-group-text"} onClick={handleBtnClick}
                                disabled={btnDisabled}> {content}</button>
                        <div className="invalid-feedback">
                            请输入正确的手机号
                        </div>
                    </FloatingLabel>

                    <div className="checkbox col">
                        <Form.Check className="" name="form-sign-up-agree" type="checkbox"
                                    id="form-sign-up-agree" required="" defaultChecked label={"已阅读并同意 用户协议 和 隐私政策"}/>
                        <div className="invalid-feedback">
                            请勾选同意协议
                        </div>
                    </div>
                </section>

                <section className={!MobileCode ? 'd-none' : 'd-block'}>
                    <div className="d-flex align-items-center  my-4">
    <span className="fa-stack fa-2x me-2">
<i className="fa-duotone fa-mobile fa-stack-2x"></i>
    <i className="fa-solid fa-message text-primary position-absolute top-0 end-0 m-2"></i>
  </span>
                        <div className="text-muted">
                            验证码已发送到您填写的手机号码上<br/>有效期5分钟，请注意查收
                        </div>
                    </div>
                    <article className={"d-flex justify-content-center flex-wrap"}><ReactCodeInput fields={4}/>

                        <div className={"w-100 text-center"}><Button className="mt-4" onClick={() => SetMobileCode(!MobileCode)}><i
                            className="fa-duotone fa-rotate-left"></i> 上一步</Button></div>
                    </article>


                </section>


            </Form>

        </>
    )
}

export {BindEmail, BindMobile}