/**
 * @description React wangEditor usage
 * @author wangfupeng
 */

import React, {useState, useEffect} from 'react'
import '../assets/SCSS/custom-wangeditor.scss'
import {Editor, Toolbar} from '@wangeditor/editor-for-react'

function Wangeditor() {
    const [editor, setEditor] = useState(null) // 存储 editor 实例
    const [html, setHtml] = useState('')

    /*    // 模拟 ajax 请求，异步设置 html
        useEffect(() => {
            setTimeout(() => {
                setHtml('')
            }, 1500)
        }, [])*/

    const toolbarConfig = {
        toolbarKeys: [
            "emotion",
            {
                key: 'group-code', // 必填，要以 group 开头
                title: '代码', // 必填
                iconSvg: "<svg viewBox=\"0 0 1280 1024\"><path d=\"M832 736l96 96 320-320L928 192l-96 96 224 224zM448 288l-96-96L32 512l320 320 96-96-224-224zM701.312 150.528l69.472 18.944-192 704.032-69.472-18.944 192-704.032z\"></path></svg>", // 可选
                menuKeys: ["code", "codeBlock",] // 下级菜单 key ，必填
            },
            {
                "key": "group-image",
                "title": "图片",
                "iconSvg": "<svg viewBox=\"0 0 1024 1024\"><path d=\"M959.877 128l0.123 0.123v767.775l-0.123 0.122H64.102l-0.122-0.122V128.123l0.122-0.123h895.775zM960 64H64C28.795 64 0 92.795 0 128v768c0 35.205 28.795 64 64 64h896c35.205 0 64-28.795 64-64V128c0-35.205-28.795-64-64-64zM832 288.01c0 53.023-42.988 96.01-96.01 96.01s-96.01-42.987-96.01-96.01S682.967 192 735.99 192 832 234.988 832 288.01zM896 832H128V704l224.01-384 256 320h64l224.01-192z\"></path></svg>",
                "menuKeys": [
                    "insertImage",
                    "uploadImage"
                ]
            },
            "insertLink",
            "|",
            "bold",
            "underline",
            "italic",
            "through",
            "color",
            "clearStyle",
            "|",
            "bulletedList",
            "numberedList",
            "todo",
            "|",
            {
                "key": "group-more-style",
                "title": "更多",
                "iconSvg": "<svg viewBox=\"0 0 1024 1024\"><path d=\"M204.8 505.6m-76.8 0a76.8 76.8 0 1 0 153.6 0 76.8 76.8 0 1 0-153.6 0Z\"></path><path d=\"M505.6 505.6m-76.8 0a76.8 76.8 0 1 0 153.6 0 76.8 76.8 0 1 0-153.6 0Z\"></path><path d=\"M806.4 505.6m-76.8 0a76.8 76.8 0 1 0 153.6 0 76.8 76.8 0 1 0-153.6 0Z\"></path></svg>",
                "menuKeys": [
                    "bgColor",
                    "sup",
                    "sub",
                    "insertVideo",
                    "insertTable",
                    "divider"
                ]
            },
            "|",
            "undo",
            "redo",
            "|",
            "fullScreen",
        ],
    }
    const editorConfig = {
        placeholder: '请输入内容...',

    }

    // 及时销毁 editor
    useEffect(() => {
        return () => {
            if (editor == null) return
            editor.destroy()
            setEditor(null)
        }
    }, [editor])


    return (
        <>
            <section className="wang-editor">
                <div id="lp-editor-toolbar" className={"editor-toolbar"}>
                    <Toolbar editor={editor}
                             defaultConfig={toolbarConfig}
                             mode="default"/>
                </div>
                <div id="lp-editor-container" className="editor-container heti"><Editor defaultConfig={editorConfig}
                                                                                        onCreated={setEditor}
                                                                                        onChange={editor => setHtml(editor.getHtml())}
                                                                                        mode="default"/>
                </div>
            </section>
            <div style={{marginTop: '15px'}}>
                {html}
            </div>
        </>
    )
}

export default Wangeditor