import Card from 'react-bootstrap/Card'
import Form from 'react-bootstrap/Form'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'
import OverlayTrigger from 'react-bootstrap/OverlayTrigger'
import Tooltip from 'react-bootstrap/Tooltip'
import Wangeditor from "../wangeditor";



export default function AccountOverview() {

    return (
        <>
            <Card className={"account-body-card"}>
                <Card.Header className={"p-4 bg-white"}>
                    <Card.Title>
                        <h5>基础信息</h5>
                    </Card.Title>
                </Card.Header>
                <Card.Body className={"p-4"}>
                    <Form className="info-form">

                        <Form.Group as={Row} className="mb-4" controlId="display_name">
                            <Form.Label column sm={3}>
                                <i
                                    className="fa-regular fa-fw  fa-user"></i> 昵称<OverlayTrigger
                                overlay={
                                    <Tooltip>
                                        昵称将在全站显示
                                    </Tooltip>
                                }
                            ><i
                                className="fa-regular fa-circle-question text-muted ms-1"></i></OverlayTrigger>
                            </Form.Label>
                            <Col sm={9}>
                                <Form.Control placeholder="昵称"/>
                            </Col>
                        </Form.Group>

                        <Form.Group as={Row} className="mb-4">
                            <Form.Label column sm={3}>
                                <i
                                    className="fa-regular fa-fw  fa-id-badge"></i> 专属铭牌<OverlayTrigger
                                overlay={
                                    <Tooltip>
                                        你的专属铭牌将显示在网站的各处，你只需要替换默认值中的标题和网址即可哦~
                                    </Tooltip>
                                }
                            ><i
                                className="fa-regular fa-circle-question text-muted ms-1"></i></OverlayTrigger>
                            </Form.Label>
                            <Col sm={9}>
                                <Row>
                                    <Col><Form.Control id={"nameplate_text"} className={"mb-2 mb-lg-0"} placeholder="标题"/></Col>
                                    <Col sm={"8"}><Form.Control id={"nameplate_url"} placeholder="链接"/></Col>
                                </Row>
                            </Col>
                        </Form.Group>

                        <fieldset>
                            <Form.Group as={Row} className="mb-3">
                                <Form.Label as="legend" column sm={3}>
                                    <i
                                        className="fa-regular fa-fw fa-venus-mars"></i> 性别
                                </Form.Label>
                                <Col sm={9}>
                                    <Form.Check
                                        type="radio"
                                        label="男"
                                        name="formHorizontalRadios"
                                        id="formHorizontalRadios1" checked
                                    />
                                    <Form.Check
                                        type="radio"
                                        label="女"
                                        name="formHorizontalRadios"
                                        id="formHorizontalRadios2"
                                    />
                                </Col>
                            </Form.Group>
                        </fieldset>

                        <Form.Group as={Row} className="mb-3">
                            <Form.Label as="legend" column sm={3}>
                                <i
                                    className="fa-regular fa-fw fa-comment-text"></i> 简介<small
                                className="text-muted">（可选 限200字）</small>
                            </Form.Label>
                            <Col sm={9}>
                                <Form.Control as="textarea" className={"d-none"}/>
                                <Wangeditor></Wangeditor>
                            </Col>
                        </Form.Group>

                        <Card.Footer className="pt-0 bg-white border-0 p-0">
                            <div className="d-flex justify-content-end gap-3">

                                <Button variant="primary"><i
                                    className="fa-duotone fa-floppy-disk fa-fw me-1"></i>保存更改</Button>
                            </div>
                        </Card.Footer>

                    </Form>
                </Card.Body>
            </Card>
        </>
    );
}



